// My Styles
.boxProfileActif {
    display: flex;
}

.light-shadow{
  // filter: drop-shadow(0px 3px 3px rgba(0, 0, 0, 0.4));
}

.lightclick {
    -webkit-animation: slide-out-top;
}

@-webkit-keyframes slide-out-top {
  0% {
      filter: none;
  }
  50% {
      filter: drop-shadow(0px 3px 3px rgba(0, 0, 0, 0.4));
  }
  100% {
      filter: none;
  }
}
@keyframes slide-out-top {
  0% {
      filter: none;
  }
  50% {
      filter: drop-shadow(0px 3px 3px rgba(0, 0, 0, 0.4));
  }
  100% {
      filter: none;
  }
}
// EndStyles

.vitesseTranslate {
    transition: transform 300ms cubic-bezier(0.455, 0.33, 0.515, 0.955)
}

.vitesseScale {
    transition: transform 300ms 
}

